



































import {Component, Vue} from 'vue-property-decorator';
import {List, Divider, Loading, Overlay} from 'vant';
import {DiamondUtil} from '../../util/diamond_util';
import 'vant/lib/list/style';
import 'vant/lib/divider/style';
import 'vant/lib/loading/style';
import 'vant/lib/overlay/style';
import {Business} from '../../model/business_model';
import {ToastUtil} from "../../util/toast";

Vue.use(List).use(Divider).use(Loading).use(Overlay);

@Component
export default class BusinessList extends Vue {

    private showList: Business[] = [];
    private cacheBusinessList: Business[] = [];
    private searchValue = '';
    private finished = true;
    private loading = false;
    private isLoadingAccount = true;        // 加载数据时的遮罩层

    public created(): void {
        this.$store.state.title = this.$t('business.list');
    }

    public mounted(): void {
        this.finished = false;
    }

    public onSearch(): void {
        console.log("onSearch===>" + this.searchValue);
        if (this.cacheBusinessList.length > 1) {
            this.showList = [];
            this.cacheBusinessList.forEach((item: Business) => {
                if ((item.name.toLowerCase() === this.searchValue.toString().toLowerCase()) ||
                    (item.address.toLowerCase() === this.searchValue.toString().toLowerCase())) {
                    this.showList.push(item);
                }
            });
            if (this.showList.length === 0) {
                ToastUtil.fail("不存在搜索结果");
            }
        } else {
            ToastUtil.fail("业务员列表为空");
        }
    }

    public onLoad(): void {
        let tThis = this;
        // let nativeAddress = "5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY";          //alice  // todo replace address
        DiamondUtil.getInstance().loadBusinessList(this.$store.state.nativeChainAddress).then((businessList: Business[]) => {
            this.cacheBusinessList = businessList;
            if (this.cacheBusinessList.length === 0) {
                ToastUtil.show("链上数据为空");
            }
            this.showList = businessList;
            this.$store.state.businessList = this.cacheBusinessList;
            this.loading = false;
            this.finished = true;
            this.isLoadingAccount = false;
        }).catch(function (error) {
            ToastUtil.fail("加载链上数据失败" + error);
            tThis.loading = false;
            tThis.finished = true;
            tThis.isLoadingAccount = false;
        });
    }

    public showBusiness(val: Business): void {
        console.log(val);
        this.$store.commit('updateNowBusiness', val);
        this.$router.push({
            name: 'business_detail',
        });
    }
}
